<template class="white-content">
  <div id="app">
    <div class="content">
      <router-view :key="$route.fullPath"></router-view>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import store from "./store";

export default {
  created: function() {
    const router = this.$router;
    const token = localStorage.getItem("art-user-token");

    this.initOneSignal();

    if (token) {
      store.commit("auth/AUTH_SUCCESS", token);
      axios.defaults.headers.common = {
        Authorization: `Bearer ${token}`
      };
      store.dispatch("user/BASE_INFO");
    }

    axios.interceptors.request.use(
      function(request) {
        return request;
      },
      function(error) {
        // Do something with request error
        return Promise.reject(error);
      }
    );

    axios.interceptors.response.use(
      function(response) {
        return response;
      },
      function(error) {
        if (error.response.status === 401) {
          error.response.data.message = "Необходимо авторизоваться";
          store.commit("auth/AUTH_LOGOUT");
          store.commit("user/CLEAR_USER");
          router.push("/login");
        }
        return Promise.reject(error);
      }
    );
  },
  methods: {
    async initOneSignal() {
      await this.$OneSignal.init({ appId: process.env.VUE_APP_ONE_SIGNAL });
      const isEnabled = await this.$OneSignal.isPushNotificationsEnabled();
      if (isEnabled) {
        console.log("OneSignal isEnabled");
        const userId = await this.$OneSignal.getUserId();
        console.log("OneSignal", userId);
      } else {
        console.log("OneSignal isDisabled");
      }
    }
  }
};
</script>

<style lang="scss"></style>
