import axios from "axios";

export default {
  namespaced: true,
  state: {
    area: []
  },
  mutations: {
    SET_AREA(state, payload) {
      state.area = payload;
    }
  },
  getters: {
    AREA(state) {
      return state.area;
    }
  },
  actions: {
    async GET_AREA({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/required-area/list`)
        .then(response => {
          commit("SET_AREA", response.data._items);
        });
    }
  }
};
