import axios from "axios";

export default {
  namespaced: true,
  state: {
    user: JSON.parse(localStorage.getItem("art-user")) || {},
    roles: {
      administrator: {
        title: "администратор"
      },
      partner: {
        title: "партнер"
      },
      resident: {
        title: "резидент"
      },
      visitor: {
        title: "посетитель"
      }
    }
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user;
      localStorage.setItem("art-user", JSON.stringify(user));
    },
    CLEAR_USER(state) {
      state.user = {};
      localStorage.removeItem("art-user-token");
    }
  },
  getters: {
    getUser: state => state.user,
    getRole: state =>
      state.user.role ? state.roles[state.user.role].title : null,
    isAdministrator: state =>
      state.user.role ? state.user.role === "administrator" : false,
    isResident: state =>
      state.user.role ? state.user.role === "resident" : false,
    isPartner: state =>
      state.user.role ? state.user.role === "partner" : false,
    isVisitor: state =>
      state.user.role ? state.user.role === "visitor" : false
  },
  actions: {
    async BASE_INFO({ commit }) {
      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/user/base-info`);

      await axios
        .get(url)
        .then(res => {
          commit("SET_USER", res.data.user);
        })
        .catch(error => {
          return Promise.reject(error.response);
        });
    },
    async USER_UPDATE({ commit, state }, data) {
      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/user/update`);
      url.searchParams.set("id", state.user.id);

      await axios
        .patch(url, data)
        .then(res => {
          commit("SET_USER", res.data);
        })
        .catch(error => {
          return Promise.reject(error.response);
        });
    },
    async RESIDENTS_LIST() {
      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/user/list`);
      url.searchParams.set("roles[]", "resident");
      return await axios.get(url).catch(error => {
        return Promise.reject(error.response);
      });
    }
  }
};
