import axios from "axios";

export default {
  namespaced: true,
  state: {
    token: localStorage.getItem("art-user-token") || "",
    status: ""
  },
  mutations: {
    AUTH_REQUEST(state) {
      state.status = "loading";
    },
    AUTH_SUCCESS(state, token) {
      state.status = "success";
      state.token = token;
    },
    AUTH_ERROR(state) {
      state.status = "error";
    },
    AUTH_LOGOUT(state) {
      state.token = "";
    }
  },
  getters: {
    isAuthenticated: state => !!state.token,
    authStatus: state => state.status
  },
  actions: {
    async GET_ENTER_CODE(context, user) {
      return await axios.post(
        `${process.env.VUE_APP_API_URL}/v1/auth/get-enter-code`,
        { phone: user.phone }
      );
    },
    async VERIFY_GET_ENTER_CODE({ commit }, user) {
      return axios
        .post(`${process.env.VUE_APP_API_URL}/v1/auth/verify-enter-code`, {
          phone: user.phone,
          code: user.sms
        })
        .then(response => {
          const token = response.data.token;
          const user = response.data.user;
          localStorage.setItem("art-user-token", token);
          commit("user/SET_USER", user, { root: true });
          commit("AUTH_SUCCESS", token);
          axios.defaults.headers.common = {
            Authorization: `Bearer ${token}`
          };
          return Promise.resolve(response);
        })
        .catch(error => {
          console.log(error.response.data);
          return Promise.reject(error.response.data.message);
        });
    },

    async LOGIN_BY_PHONE_CODE(context, user) {
      return await axios.post(
        `${process.env.VUE_APP_API_URL}/v1/auth/get-login-by-phone-code`,
        { phone: user.phone }
      );
    },
    async VERIFY_LOGIN_BY_PHONE_CODE({ commit }, user) {
      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/auth/verify-login-by-phone-code`,
          { phone: user.phone, code: user.sms }
        )
        .then(responce => {
          const token = responce.data.token;
          const user = responce.data.user;
          commit("user/SET_USER", user, { root: true });
          localStorage.setItem("art-user-token", token);
          commit("AUTH_SUCCESS", token);
          axios.defaults.headers.common = {
            Authorization: `Bearer ${token}`
          };
          return Promise.resolve();
        })
        .catch(error => {
          console.log(error.response.data);
          return Promise.reject(error.response.data.message);
        });
    },

    async REGISTRATION_BY_PHONE_CODE(context, user) {
      return await axios.post(
        `${process.env.VUE_APP_API_URL}/v1/auth/get-registration-by-phone-code`,
        { phone: user.phone }
      );
    },

    async VERIFY_REGISTRATION_BY_PHONE_CODE({ commit }, user) {
      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/auth/verify-registration-by-phone-code`,
          { phone: user.phone, code: user.sms }
        )
        .then(responce => {
          const token = responce.data.token;
          const user = responce.data.user;
          localStorage.setItem("art-user-token", token);
          commit("AUTH_SUCCESS", token);
          commit("user/SET_USER", user, { root: true });
          axios.defaults.headers.common = {
            Authorization: `Bearer ${token}`
          };
          return Promise.resolve();
        })
        .catch(error => {
          console.log(error.response.data);
          return Promise.reject(error.response.data.message);
        });
    },

    async AUTH_LOGOUT({ commit }) {
      commit("AUTH_LOGOUT");
      commit("user/CLEAR_USER", null, { root: true });
      localStorage.removeItem("art-user");
      delete axios.defaults.headers.common["Authorization"];
    }
  }
};
