import axios from "axios";

export default {
  namespaced: true,
  state: {
    place: []
  },
  mutations: {
    SET_PLACE(state, payload) {
      state.place = payload;
    }
  },
  getters: {
    PLACE(state) {
      return state.place;
    }
  },
  actions: {
    async GET_PLACE({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/place/list`)
        .then(response => {
          commit("SET_PLACE", response.data._items);
        });
    }
  }
};
