import axios from "axios";

export default {
  namespaced: true,
  state: {
    startup: []
  },
  mutations: {
    SET_STARTUP(state, payload) {
      state.startup = payload;
    }
  },
  getters: {
    STARTUP(state) {
      return state.startup;
    }
  },
  actions: {
    async GET_STARTUP({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/startup/list`)
        .then(response => {
          commit("SET_STARTUP", response.data._items);
        });
    }
  }
};
