import axios from "axios";

export default {
  namespaced: true,
  state: {
    category: []
  },
  mutations: {
    SET_CATEGORY(state, payload) {
      state.category = payload;
    }
  },
  getters: {
    CATEGORY(state) {
      return state.category;
    }
  },
  actions: {
    async GET_CATEGORY({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/category/list`)
        .then(response => {
          commit("SET_CATEGORY", response.data._items);
        });
    },
    async GET_CATEGORY_POST({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/category/filter-post`)
        .then(response => {
          commit("SET_CATEGORY", response.data._items);
        });
    },
    async GET_CATEGORY_EVENT({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/category/filter-event`)
        .then(response => {
          commit("SET_CATEGORY", response.data._items);
        });
    },
    async GET_CATEGORY_RESIDENTS_EVENT({ commit }, id) {
      let url = new URL(
        `${process.env.VUE_APP_API_URL}/v1/category/filter-event`
      );
      url.searchParams.set("resident_id", id);
      await axios.get(url).then(response => {
        commit("SET_CATEGORY", response.data._items);
      });
    },
    async GET_CATEGORY_STARTUP({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/category/filter-startup`)
        .then(response => {
          commit("SET_CATEGORY", response.data._items);
        });
    },
    async GET_CATEGORY_RESIDENT({ commit }) {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/category/filter-resident`)
        .then(response => {
          commit("SET_CATEGORY", response.data._items);
        });
    }
  }
};
